* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

html, body {
    background-color: #0a0a23;
}

.App {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding-top: 15px;
    font-family: sans-serif;
}

.freecodecamp-logo-contedenedor {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 30px 10px 0px;
}

.contenedor-principal {
    height: 550px;
    min-width: 600px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}