button {
  width: 300px;
  height: 60px;
  font-family: sans-serif;
  font-size: 18px;
  font-weight: bold;
  color: #fff;
  border: 2px solid #fff;
  border-radius: 14px;
  margin: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #00471b;
}

.boton-clic {
  background-color: #002ead;
}

.boton-reiniciar {
  background-color: #5a01a7;
}